    description:
      "一直以來我都對公關行銷相關的工作很感興趣，透過理性數據的支撐，並用天馬行空的包裝達到宣傳的目的，這也是先勢博報堂集團的優勢，既有自主研發的數據平台，讓公關人員能解析出客戶想要的資訊，同時團隊內活潑的氣氛，更是我們激盪出創意想法的最大原因。\n一直以來我都認為公司文化是影響留職意願的主因，而當我加入先勢博報堂集團時就很欣賞內部暢通的溝通管道，和同事間緊密的感情，而這樣的文化一直都沒有改變，這也是我持續在這裡打拼的原因。\n在公關公司工作有機會接觸不同產業的客戶，藉此吸收豐富的知識、辦多元的活動，同時和不同線別的媒體和廠商打交道，對於喜歡挑戰的我來說，這樣的工作充滿樂趣。未來我將繼續培養公關能力，成為值得信賴的公關人。",
