      "「公關就是一個集結所有星座的好與壞，一份讓你又愛又恨的工作」\n因緣際會下，於大學二年級暑期實習來到先勢公關，經過兩個月的實戰經驗後，更確立這是自己畢業後嚮往的工作。\n「公關」真的是讓人又愛又恨的一份工作，平常大家看到的我們，應該都是在記者會上穿著光鮮亮麗的正裝，穿梭於會場中;但在 背後是與團隊沒日沒夜的加班趕工，只為客戶及媒體帶來一場精彩的記者會，我想這就是公關迷人的所在-團隊合作與成就感，\n「溝通」在這份工作我覺得是不可或缺的必修學分，無論是對內同仁，或是對外廠商及客戶，如何有效(笑)溝通是我在先勢博報堂 就業過程中，學到非常寶貴的一件事。好的溝通可以讓你上天堂，無效溝通只會讓你在地獄無限輪迴。\n期待在未來的工作中，可以努力增進更多不同的知識，持續創造自己未來的價值!",
