// 滑動
const scrollToMain = () => {
  if (main.value) {
    const topPos = 0;
    main.value.scrollTo({
      top: topPos,
      behavior: "smooth",
    });
  }
};

const scrollToCareer = () => {
  if (main.value) {
    const topPos = career.value.getBoundingClientRect().top;
    main.value.scrollTo({
      top: topPos,
      behavior: "smooth",
    });
  }
};

// ==== 擺動動畫旗標 =====================================

// 標題 EVERYONE COULD BE A STRONG INFLUENCER 擺動
// 獲取位置
const titleSlogan = ref(null);
const titleCreativity = ref(null);
const titleCareer = ref(null);
const titleHeroes = ref(null);
const titleWorkplace = ref(null);
const titleGroup = ref(null);
const titleTalent = ref(null);
// 是否擺動過
const titleSloganActioned = ref(false);
const titleCreativityActioned = ref(false);
const titleCareerActioned = ref(false);
const titleHeroesActioned = ref(false);
const titleWorkplaceActioned = ref(false);
const titleGroupActioned = ref(false);
const titleTalentActioned = ref(false);
// 是否擺動中
const isSloganSwinging = ref(false);
const isCreativitySwinging = ref(false);
const isCareerSwinging = ref(false);
const isHeroesSwinging = ref(false);
const isWorkplaceSwinging = ref(false);
const isGroupSwinging = ref(false);
const isTalentSwinging = ref(false);

const main = ref(null);

const titleSloganAction = () => {
  const titleSloganPos = titleSlogan.value.getBoundingClientRect();
  if (
    window.scrollY + 625 > titleSloganPos.top + window.scrollY &&
    !titleSloganActioned.value
  ) {
    titleSloganActioned.value = true;
    isSloganSwinging.value = true;
    setTimeout(() => {
      isSloganSwinging.value = false;
    }, 1000); // 動畫持續時間
  }
};

