// ==== 主視覺動畫 =====================================
const startAnimation = () => {
  // 循環遍歷所有配置項
  mainSloganAnimateConfig.value.forEach((config, index) => {
    // 為每個元素設置一個延時，延時時間隨索引增加
    setTimeout(() => {
      const element = document.getElementById("sloganAnimate" + index);
      if (element) {
        // 將元素從隱藏改為顯示
        element.style.display = "block";

        // 如果是最後一個元素
        if (index === mainSloganAnimateConfig.value.length - 1) {
          // 等待0.3秒後關閉覆蓋層
          setTimeout(() => {
            document.getElementById("sloganOverlay").style.display = "none";
            isRunMainAnimate.value = true;
          }, 1000); // 這裡確保是在最後一個元素顯示後再等待0.3秒
        }
      }
    }, index * 300); // 每個元素之間等待0.3秒
  });
};
